/* global */
$color-one: #ff9f1a;
$color-two: #302d2d;
$color-three: #4b4b4b;
$color-four: #ececec;

$font-family: 'Abril Fatface', cursive;


body {
    margin: 0;
    font-size: 16px;
    line-height: 1.7;
    text-align: center;
    font-family: 'Abril Fatface', cursive;
    color: $color-three; 
}

  .container {
    max-width: 1140px;
    padding: 0 20px;
    margin: 0 auto; 
}

/* navigation */
.navigation {
    padding: 12px 0;
    overflow: hidden;
}

.navigation ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    float: right;
}
.navigation li {
    display: inline-block;
    margin: 0 36px 0 0;
    position: relative;
}

.navigation a {
    text-decoration: none;
    color: $color-four;
}

/* splash */

.btn {
    display: inline-block;
    min-width: 160px;
    border-radius: 5px;
    font-size: 16px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border-width: 5px;
    background-color: $color-four;
    background-position: 100% 0;
    background-size: 300%;
}

.main-page {
    height: 70vh;
    position: relative;
    background-image: url(https://i.postimg.cc/TYgf8S26/budynek.jpg);
    background-position: center center;
    background-size: cover;
    background-attachment: inherit;
}

.splash {
    padding: 7%;
}
.splash-inner {
    margin: 0;
}

.page-title {
    font-size: 40px;
    word-spacing: 4px;
    font-weight: normal;
    line-height: 1.2;
    color: $color-four;
    text-align: center;
    
}

.page-title span {
    font-size: 60px;
    font-weight: bold;
    text-align: center;
    color: $color-one;
}

/* Services */

.services {
    margin: 100px 0 0 0;
}

.services i {
    font-size: 40px;
    background: #fff;
    width: 100px;
    height: 100px;
    line-height: 100px;
    color: $color-one;
    border-radius: 50%;
    border-style: solid;
}

.row {
    padding: 50px 0 0 0;
}

.services h3 {
    font-size: 25px;
}

/* Portfolio */

.PORTFOLIO {
    background-color: #f0f0f0;
    margin: 100px 0 0 0;
    padding: 100px 0;
}

.card img {
    width: 300px;
    height: 300px;
    object-fit: cover;
}

.card {
    background-color: #ffffff;
    width: 300px;
    height: 400px;
    margin: auto;
}

/* footer */

.media {
    font-size: 0px;
}

.footer .row {
    margin: 10px 0;
    padding: 10px 0;
}

.footer i {
    margin: 0 10px;
    font-size: 20px;
    background: $color-three;
    width: 40px;
    height: 40px;
    line-height: 40px;
    color: $color-four;
    border-radius: 50%;
    flex: 0 0 33.333%;
}

@media (max-width: 767px) {

    .footer i {
        font-size: 10px;
        width: 20px;
        height: 20px;
        line-height: 20px;
    }

    .footer {
        font-size: 10px;
    }

    .card img {
        width: 100%;
        height: 300px;
        object-fit: cover;
    }
    
    .card {
        background-color: #ffffff;
        width: 100%;
        height: 400px;
        margin: auto;
    }

}

@media (min-width: 768px) and (max-width: 917px) {

    .footer i {
        font-size: 20px;
        width: 40px;
        height: 40px;
        line-height: 40px;
    }
}